.ng-course-content{width:80%;margin:auto;display:flex;justify-content:space-between;margin-bottom:200px}@media screen and (max-width: 768px){.ng-course-content{width:100%}}.ng-course-content h2{text-align:center}.ng-course-content>.content{margin-right:24px;padding:27px;min-height:100vh;width:100%;height:-webkit-fit-content;height:-moz-fit-content;height:fit-content;display:flex;flex-direction:column;align-items:center;background:#ffffff;box-shadow:0px 12px 18px rgba(41,94,255,0.05);border-radius:18px}@media screen and (max-width: 768px){.ng-course-content>.content{margin-right:0px;margin-top:24px}}.ng-course-content>.content>.markdown{width:80%;padding:24px;padding-top:24px;font-family:Ubuntu;box-shadow:0px 1px 3px 0px rgba(0,0,0,0.2),0px 1px 1px 0px rgba(0,0,0,0.14),0px 2px 1px -1px rgba(0,0,0,0.12);margin-bottom:32px}@media screen and (max-width: 768px){.ng-course-content>.content>.markdown{width:100%}}.ng-course-content>.content>.github-link{color:gray;text-decoration:none;width:80%;text-align:right}.ng-course-content>.content>.arrow-row{padding:32px 32px;width:80%;display:flex;justify-content:space-between}

